@include ipad-portrait {

    .woocommerce-wishlist {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 17px 25px;
        }
    }
}

@include phone-landscape {

    .woocommerce-wishlist {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 2em;

            a {
                float: none;
                margin: 0 70% 15px 0;
            }
        }
    }
}