/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-landscape {

	.mkdf-woocommerce-page {

		table.cart {

			td.actions {

				.coupon {

					.input-text {
						padding: 8px 48px;
					}
				}
			}
		}
	}
}

@include ipad-portrait {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {
					
					.input-text {
						width: 180px;
						margin: 0 17px 0 0;
						padding: 8px 20px;
					}
					
					.button {
						margin: 0;
					}
				}
				
				> .button {
					margin: 0;
				}
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 16px;
					
					.input-text {
						width: 100%;
						padding: 8px 15px;
						margin: 0 10px 20px 0;
						text-align: center;
					}
				}
				
				.coupon,
				> input,
				.button {
					float: none;
					display: block;
					width: 100%;
				}

				.button {
					margin: 0;
				}
			}
		}

		div.woocommerce a.checkout-button {
			width: 100%;
			text-align: center;
		}
	}
}

@include phone-portrait {
	
	.mkdf-woocommerce-page {
		
		.mkdf-content {
			
			.woocommerce-cart-form {
				
				.mkdf-quantity-buttons {
					
					.mkdf-quantity-minus,
					.mkdf-quantity-plus,
					.mkdf-quantity-input {
						width: 30px;
						height: 30px;
						line-height: 28px;
						margin: 0 -1px 0 0;
					}

					.mkdf-quantity-minus,
					.mkdf-quantity-plus {
						display: none;
					}

					.mkdf-quantity-input {
						font-size: 15px;
					}
				}
			}
		}
		
		table.cart {
			
			thead tr {
				
				th {
					font-size: 11px;
				}
			}

			td.actions {
				padding: 43px 0 33px;
				
				> .button {
					margin-top: 17px;
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */