/* ==========================================================================
   WooCommerce Message Responsive styles - begin
   ========================================================================== */


@include phone-landscape {
    
    .mkdf-woocommerce-page {
        
        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 25px 19px 15px;
            line-height: 1.375em;

            a.button,
            a.wc-forward:not(.added_to_cart):not(.checkout-button) {
                top: -6px;
                float: left;
                margin: 0 55% 10px 0;
            }
        }

        &.woocommerce-account {

            .woocommerce-message,
            .woocommerce-info,
            .woocommerce-error {

                a.button,
                a.wc-forward:not(.added_to_cart):not(.checkout-button) {
                    margin: 0 62% 10px 0;
                }
            }

            &.woocommerce-orders {

                .woocommerce-message,
                .woocommerce-info,
                .woocommerce-error {

                    a.button,
                    a.wc-forward:not(.added_to_cart):not(.checkout-button) {
                        margin: 0 48% 10px 0;
                    }
                }
            }
        }
    }
}

