/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */

@include ipad-landscape {

    .woocommerce-page .mkdf-content,
    div.woocommerce {

        .mkdf-quantity-buttons {

            .mkdf-quantity-input,
            .mkdf-quantity-minus,
            .mkdf-quantity-plus {
                width: 48px;
                height: 48px;
                font-size: 20px;
                line-height: 44px;
            }
        }
    }
}

@include phone-portrait {

    .woocommerce-page .mkdf-content,
    div.woocommerce {

        .mkdf-quantity-buttons {

            .mkdf-quantity-minus,
            .mkdf-quantity-plus {
                margin-bottom: 10px;
            }
        }
    }
}   
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */