/* Woocommerce Product Page shortcode responsive style - begin
========================================================================== */

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {

    div.woocommerce {

        > .single-product {

            .woocommerce-tabs {

                ul.tabs {

                    > li {

                        a {
                            padding: 12px 7px;
                        }
                    }
                }
            }
        }
    }
}

@include ipad-landscape {

    div.woocommerce {

        > .single-product {

            .mkdf-single-product-content {

                .images,
                .mkdf-single-product-summary {
                    float: none;
                    width: 100%;
                    padding: 0;
                }

                .images {

                    .woocommerce-product-gallery__trigger {
                        right: 10px;
                    }
                }

                .mkdf-single-product-summary {
                    margin: 30px 0 0;
                }
            }

            .woocommerce-tabs {

                ul.tabs {

                    > li {

                        a {
                            padding: 13px 10px;
                        }
                    }
                }
            }
        }
    }
}

@include phone-landscape {

    div.woocommerce {

        > .single-product {

            .mkdf-single-product-content {

                .images {

                    &.woocommerce-product-gallery--columns-4 {

                        .woocommerce-product-gallery__image {

                            &:not(:first-child) {
                                width: 50%;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* Woocommerce Product Page shortcode responsive style - end
========================================================================== */